import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { IonicPage, NavController } from 'ionic-angular';

@IonicPage({
  segment: 'cart/step3'
})
@Component({
  selector: 'page-cart-step3',
  templateUrl: 'cart-step3.html',
})
export class CartStep3Page {
  form: FormGroup;

  constructor(
    private fb: FormBuilder,
    public navCtrl: NavController
  ) {
    this.createForm();
  }

  private createForm(){
    this.form = this.fb.group({
      state: ['', [
        Validators.required
      ]],
      city: ['', [
        Validators.required
      ]],
      addressLine1: ['', [
        Validators.required
      ]],
      addressLine2: ['', []],
      zip: ['', [
        Validators.required
      ]]
    });
  }

  onFormSubmit(){
    if(this.form.invalid){ return; }

    this.navCtrl.setRoot('CartStep4Page');
  }
}



